:root {
  --font: 'Nunito Sans', sans-serif;
  --mobile-inline: 30px;
  --desktop-inline: 70px;
  --max-width: 1450px;
}

.App {
  min-height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font);
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}